import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { skip, takeUntil } from 'rxjs/operators';

import { FooterPagesService } from '@services/modules/footer-pages/dataservice/footer-pages.service';
import { SettingsService } from '@services/services/settings-service';
import { FooterPagesContent } from '@schemas/modules/footer-pages/footer-pages.model';

@Component({
    selector: 'tz-terms-and-conditions',
    templateUrl: './terms-and-conditions.component.html',
    styleUrls: ['./terms-and-conditions.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class TermsAndConditionsComponent implements OnInit, OnDestroy {
    public content: FooterPagesContent | undefined;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private footerPagesService: FooterPagesService, private settingsService: SettingsService) {}

    ngOnInit() {
        this.getContent();

        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.getContent();
        });
    }

    public getContent(): void {
        this.footerPagesService.getPageContent('terms').subscribe((content) => {
            this.content = content;
        });
    }

    public onDownloadPDF(): void {
        this.footerPagesService.downloadPDF().subscribe((res) => {
            const newBlob = new Blob([res], { type: 'application/pdf' });
            const data = window.URL.createObjectURL(newBlob);

            const link = document.createElement('a');
            link.href = data;
            link.download = 'termsAndConditions.pdf';
            link.dispatchEvent(new MouseEvent('click', { bubbles: true, cancelable: true, view: window }));

            setTimeout(() => {
                window.URL.revokeObjectURL(data);
                link.remove();
            }, 100);
        });
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }
}
